const SEND_MESSAGE = 'SEND-MESSAGE';

let initialState = {
        messages:[
            { id: 1, message: "" },
            { id: 2, message: " ?" },
            { id: 3, message: "   " },
            { id: 4, message: " " },
            { id: 5, message: "" },
            { id: 6, message: "" },
        ],
        dialogs:[
            { id: 1, name: "Lera" },
            { id: 2, name: "Bob" },
            { id: 3, name: "Ash" },
            { id: 4, name: "Mercy" },
            { id: 5, name: "Dva" },
            { id: 6, name: "Aboba" },
        ]
    }
const dialogsReducer = (state = initialState, action) => {
    switch(action.type) {
        case SEND_MESSAGE: {
            return {
                ...state,
                messages : [...state.messages,{id:7,message: action.newMessageText}]
            };
        }

        default:
            return state;
    }  
}

export default dialogsReducer;
export const sendMessageActionCreator = (newMessageText) =>  ({type: SEND_MESSAGE,newMessageText})
